body {
  font-family: sans-serif;
  color: white;
  background-color: #0a090b;
  margin: 0;
  padding: 1rem;
}

#root {
  display: flex;
  flex-direction: column;
  border: 0.2rem solid #1b191d;
  border-radius: 0.2rem;
}

/*
*   Navbar
*/
nav {
  flex: 1;
  background-color: #1b191d;
}
nav ul {
  padding: 1rem;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.menu-item {
  padding: 0.5rem 1rem;
  margin: 0 0.5rem;
  transition: ease 0.3s;
  border-radius: 0.1rem;
}
.menu-item:hover {
  background-color: #8132ad;
}

/*
*   Introduction
*/

#introduction {
  flex: 1;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}
#icon-div {
  display: flex;
  justify-content: flex-end;
}
.icon,
svg {
  flex: 1 1 90%;
  max-width: 5rem;
  min-width: 3rem;
  max-height: 5rem;
  align-self: center;
}
#logo {
  flex: 1;
  max-width: 22rem;
  min-width: 3rem;
  max-height: 15rem;
  align-self: center;
}
#info {
  margin: 0 0 0 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#info p,
h1 {
  margin: 0.5rem 0;
  padding: 0;
}

/*
*   Skills
*/

#skills {
  flex: 1;
  padding: 3rem 5rem 3rem 5rem;
}

#skill-grid {
  padding: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  border-radius: 0.4rem;
  background-color: #1b191d;
}

