.skill {
  background-color: #0a090b;
  overflow: hidden;
  padding: 2rem;
  border-radius: 0.3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
  border: 2px solid white;
}

.expanded {
  width: 15rem;
  height: 8rem;
  background-color: #0a090b;
  position: relative;
  left: 0;
  top: 100%;
  z-index: 999999;
}

.icon-div3 {
  max-height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 845px) {
  .expanded {
    width: 10rem;
    height: 10rem;
  }
  .expanded-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
}

@media only screen and (max-width: 617px) {
  .icon-div3 {
    width: 1rem;
    height: 2rem;
  }
  .expanded {
    width: 8rem;
    height: 10rem;
  }
}

/*
*   Experience
*/

#experience {
  flex: 1;
  display: flex;
  justify-content: center;
  background-color: #1b191d;
  padding: 1rem;
}
#experience p {
  width: 50%;
  margin: 0;
  padding: 0;
}
/*
*   Contact Form
*/

#contact {
  flex: 1;
  padding: 1rem;
  display: flex;
  justify-content: center;
}

#form {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#form h2 {
  align-self: center;
}

#icon-div2 {
  flex: 1;
  display: flex;
  justify-content: center;
}

#logoC {
  flex: 1;
  max-width: 22rem;
  min-width: 3rem;
  max-height: 15rem;
}

@media only screen and (max-width: 700px) {
  #icon-div2 {
    padding-left: 1rem;
    width: 25%;
  }
  #icon-div {
    padding-left: 0.5rem;
  }
  #logo {
    width: 25%;
  }
}

input,
textarea {
  color: white;
  background-color: #1b191d;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.2rem;
  margin-bottom: 1rem;
  resize: none;
  transition: ease 0.3s;
}

input:hover,
textarea:hover {
  background-color: #141316;
}

input:focus,
textarea:focus {
  outline: none;
  background-color: #141316;
}

button {
  align-self: center;
  color: white;
  background-color: #8132ad;
  border: 2px solid #1b191d;
  border-radius: 0.2rem;
  margin-bottom: 1rem;
  padding: 0.6rem 3rem;
  transition: ease 0.3s;
}

button:hover {
  background-color: #53206f;
  transform: scale(1.1);
}
/*
*   Footer
*/
footer {
  flex: 1;
  display: flex;
  justify-content: space-between;
  background-color: #1b191d;
}

footer h3 {
  margin-left: 1rem;
  align-self: center;
}

#social {
  margin: 0;
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: flex-end;
}

.network {
  padding: 1rem;
  margin: 0;
  transition: ease 0.5s;
}

#social li:first-child {
}
#social li:nth-child(2) {
  background-color: #ffffff;
}
#social li:nth-child(3) {
}

#social li:first-child:hover {
  background-color: #8132ad;
  transform: scale(1.1);
}

#social li:nth-child(2):hover {
  background-color: #8f91a2;
  transform: scale(1.1);
}
#social li:nth-child(3):hover {
  background-color: #8132ad;
  transform: scale(1.1);
}
